import { sendAjax } from '@/common/js/sendAjax.js';
import config from '@/apiConfig';
const {getCleanerDeail} = config.api;
export default {
	data() {
		return {
			data:[],
			cleanerDetail:[],
			sum:0
		};
	},
	methods: {
		onLoad: function(e) {
			this.data = JSON.parse(e.data)
			console.log(this.data)
			this.getCleanerDeail();
			this.calculation();
		},
		calculation(){
			var num = this.data.area;
			this.sum = num * 100; 
		},
		getCleanerDeail(){
			var that = this;
			console.log(this.data.clearnerId)
			let infoOpt = {
				url: getCleanerDeail,
				type: 'POST',
				data: {
					id:this.data.clearnerId
				}
			};
			let infoCb = {};
			infoCb.success = function(res) {
				console.log(res)
				that.cleanerDetail = res;
				uni.hideLoading()
			},
			infoCb.beforeSend = () => {
			  uni.showLoading({
			  	title:'加载中'
			  })
			};
			sendAjax(infoOpt, infoCb);
		},
		//下一页
		toSub() {
			uni.navigateTo({
				url: '../subscribeSub/subscribeSub'
			});
		}
	}
};
